import { useState } from 'react';

function Futter() {
    let [auto, setAuto] = useState(true);

    const Notification = () => {
        if (auto) {
            setAuto(false)
        } else {
            setAuto(true)
        }
    }

    return (
        <div className='futter_back'>
            <div className='futter_wrap'>
                <div className='futter_img'><img src={process.env.PUBLIC_URL + '/main_img/futter.png'} /></div>
                <div className='futter_button cursorPointer' onClick={() => { Notification() }}>
                    {
                        auto == true ? <img src={process.env.PUBLIC_URL + '/main_img/alarm.png'} /> : <img src={process.env.PUBLIC_URL + '/main_img/alarm(2).png'} />
                    }
                </div>
            </div>
        </div>
    )
}

export default Futter;