import './Main.css';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import { SubSlide, SubSlide2 } from "./SubSlide.js";
import { Header, UpHeader } from "./Header.js";
import Futter from "./futter";
import { Login, Join, Welcome } from "Login_Join/login.js";
import { Mypage } from "Mypage/Mypage.js";
import { Lovepage } from "Mypage/Lovepage.js";
import { Shop } from "ShopPage/shop.js";
import { Order } from "ShopPage/order.js";
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

function App() {

  let [탭, 탭변경] = useState(0);
  const state = useSelector((state) => { return state.userInfo })

  let 꺼낸거 = sessionStorage.getItem('LoginData');
  꺼낸거 = JSON.parse(꺼낸거);

  return (

    <div className="App">
      <UpHeader />

        <Routes>
          <Route path="/" element={
            <>
              <Header />
              <Mainimg 탭={탭} 탭변경={탭변경} />

              <SubSlide />

              <SubContent />
              <SubContent2 />
              <SubSlide2 />
              <Futter />
            </>
          } />
          <Route path="/login" element={<Login />} />
          <Route path="/Join" element={<Join />} />
          <Route path="/Mypage" element={<Mypage />} />
          <Route path="/Lovepage" element={<Lovepage />} />
          <Route path="/Welcome" element={<Welcome />} />
          <Route path="/Shop/:link" element={<Shop />} />
          <Route path="/Order" element={<Order />} />
        </Routes>

    </div >
  );
}

function Mainimg({ 탭, 탭변경 }) {
  let TOTAL_SLIDE = 2;
  let [auto, setAuto] = useState(true);

  const autoSlide = () => {
    if (auto) {
      탭변경((탭) => (탭 + 1) % 3);
    }
  };

  const playSlide = () => {
    // setAuto((auto) => (auto === true ? false : true));
    if (auto) {
      setAuto(false);
    } else if (!auto) {
      setAuto(true);
    }
  }

  const startSlide = () => {
    setAuto(true);
  }

  const stopSlide = () => {
    setAuto(false);
  }

  const nextSlide = () => {
    탭변경((탭) => (탭 < TOTAL_SLIDE ? 탭 + 1 : 0));
  }

  const prevSlide = () => {
    탭변경((탭) => (탭 === 0 ? TOTAL_SLIDE : 탭 - 1));
    // 현재 슬라이드를 눌렀는데 그게 0이였음 그러면 토탈값을 넣어주고 아니면 -1하셈
  }

  useEffect(() => {
    let a = setTimeout(() => { autoSlide() }, 7000);
    // autoSlide()함수를 호출하기 전에 auto의 상태를 확인하게 시켜야 됨
    return () => {
      clearTimeout(a);
    }
  }, [탭, auto])

  console.log('process.env.PUBLIC_URL : ' + process.env.PUBLIC_URL)

  return (
    <div className="main_slider">


      <div className={'slider-container'}>
        <div className={`slide ${탭 === 0 ? 'active' : ''}`}>
          <img src={process.env.PUBLIC_URL + "/main_img/main_1.png"} /></div>

        <div className={`slide ${탭 === 1 ? 'active' : ''}`}>
          <img src={process.env.PUBLIC_URL + "/main_img/main_2.png"} /></div>

        <div className={`slide ${탭 === 2 ? 'active' : ''}`}>
          <img src={process.env.PUBLIC_URL + "/main_img/main_3.png"} /></div>

        <div className="main_button_wrap">
          {
            Array.from({ length: 3 }).map((item, index) => {
              return (
                <div key={index} onClick={() => { 탭변경(index) }} className={`main_button cursorPointer ${탭 === index ? 'main_color' : ''}`}></div>
              )
            })
          }

          <div onClick={() => { prevSlide() }} className="main_button_round cursorPointer">
            <img src={process.env.PUBLIC_URL +  "/main_img/main_button_1.png"} />
          </div>

          <div onClick={() => { nextSlide() }} className="main_button_round cursorPointer">
            <img src={process.env.PUBLIC_URL + "/main_img/main_button_2.png"} />
          </div>

          <div onClick={() => { playSlide() }} className="main_button_round cursorPointer">
            {
              auto == true ? <img src={process.env.PUBLIC_URL + "/main_img/main_button_3.png"} /> : <img src={process.env.PUBLIC_URL + "/main_img/main_button_4.png"} />
            }
          </div>
        </div>

      </div>

    </div>
  )
}

