import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { newChange } from '../store.js';
import './mypage.css';

function Mypage() {
    let [myinfo, setMyinfo] = useState({ id: '', password: '', name: '', number: '' });
    const userInfo = useSelector((state) => { return state.userInfo })
    let 꺼낸거 = sessionStorage.getItem('LoginData');
    꺼낸거 = JSON.parse(꺼낸거);

    console.log('userInfo : ' + userInfo)
    console.log('꺼낸거' + 꺼낸거)

    const location = useLocation();
    const thepage = ['/Mypage', '/Lovepage', '/Shoppage', '/Orderpage'];
    const [mypage, setMypage] = useState(false);
    const [lovepage, setLovepage] = useState(false);
    const [Shoppage, setShoppage] = useState(false);
    const [Orderpage, setOrderpage] = useState(false);

    const [modal, setModal] = useState(false);
    const [modalValue, setModalvalue] = useState('');
    const [newValue, setNewValue] = useState('');
    const [checkModal, setCheckModal] = useState(false);
    // const [newName, setNewName] = useState('');

    const locationData = location.pathname;

    const dispatch = useDispatch();

    useEffect(() => {
        console.log('세션' + 꺼낸거.id);
        console.log('회원가입', userInfo);

        setMypage(false);
        setLovepage(false);
        setShoppage(false);
        setOrderpage(false);

        for (let i = 0; i < thepage.length; i++) {
            if (locationData === thepage[i]) {
                if (locationData == '/Mypage') {
                    setMypage(true);
                } else if (locationData == '/Lovepage') {
                    setLovepage(true);
                }
            }
        }

        const user = userInfo.find((userInfo) => userInfo.id === 꺼낸거.id)
        if (user) {
            console.log('user : ' + user.id)
            setMyinfo({
                id: user.id,
                password: user.password,
                name: user.name,
                number: user.number
            })
        } else {
            setMyinfo({
                id: '',
                password: '',
                name: '',
                number: ''
            })
        }
    }, [꺼낸거.id, userInfo]);

    const modalEvent = ((value) => {
        // console.log('value : ' + value)
        setModalvalue(value)
        setModal(true);
    })

    const onChangeValue = useCallback((e) => {
        // const phoneRegExp = /^01(?:0|1|[6-9])[.-]?(\d{3}|\d{4})[.-]?(\d{4})$/;
        const result = e.target.value;
        setNewValue(result)
        // setIsNumber(true);
    }, [])

    // const checkNewValue = (() => {
    //     if (newValue == modalValue) {
    //         setCheckModal(false);
    //     } else {
    //         setCheckModal(true);
    //     }
    // })


    return (
        <>
            <div className='Mypage_title_back'>
                <div className='Mypage_title'>
                    <img src={process.env.PUBLIC_URL + '/mypage_img/my_img.png'} />
                    <div className='Mypage_text'>{꺼낸거.id}님 어서오세요.</div>
                </div>
            </div>

            <div className='Mypage_wrap'>

                <div className="Mypage_info_wrap">
                    <div className='Mypage_info'>
                        <ul>
                            <li><Link className={`${mypage ? 'color' : ''}`} to="/Mypage">내정보</Link></li>
                            <li><Link className={`${Shoppage ? 'color' : ''}`} to="/Order">장바구니</Link></li>
                            <li><Link className={`${lovepage ? 'color' : ''}`} to="/Lovepage">좋아요</Link></li>
                        </ul>
                    </div>

                    <div className='Mypage_input_wrap'>
                        <div className="Mypage_input">
                            <div></div>
                            <input type="text" value={myinfo.id || ''} placeholder={myinfo.id || ''} />
                            {/* <img className='cursorPinter' onClick={() => modalEvent(myinfo.id)} src={'./mypage_img/수정버튼.png'} /> */}
                        </div>

                        <div className='Mypage_input'>
                            <input type="text" value={myinfo.password || ''} placeholder={myinfo.password || ''} />
                            {/* <img className='cursorPinter' src={'./mypage_img/수정버튼.png'} /> */}
                        </div>

                        <div className='Mypage_input'>
                            <input type="text" value={myinfo.name || ''} placeholder={myinfo.name || ''} />
                            <img className='cursorPinter' onClick={() => modalEvent(myinfo.name)} src={process.env.PUBLIC_URL + '/mypage_img/modify_button.png'} />
                        </div>

                        <div className='Mypage_input'>
                            <input type="text" value={myinfo.number || ''} placeholder={myinfo.number || ''} />
                            <img className='cursorPinter' onClick={() => modalEvent(myinfo.number)} src={process.env.PUBLIC_URL + '/mypage_img/modify_button.png'} />
                        </div>
                    </div>

                </div>

                {
                    modal ?
                        <div className='Mypage_modal'>
                            <div className='Mypage_modal_content'>
                                <input type="text" value={modalValue} placeholder={modalValue} />
                                <input type="text" value={newValue} placeholder='수정할 내용을 입력해주세요.' onChange={onChangeValue} />
                                <button onClick={() => {
                                    if (newValue !== modalValue && modalValue === myinfo.number) {
                                        dispatch(newChange({ id: myinfo.id, password: myinfo.password, name: myinfo.name, number: newValue }))
                                        setModalvalue('')
                                        setModal(false)
                                    } else if (newValue !== modalValue && modalValue === myinfo.name) {
                                        dispatch(newChange({ id: myinfo.id, password: myinfo.password, name: newValue, number: myinfo.number }))
                                        setModalvalue('')
                                        setModal(false)
                                    } else {
                                        alert('두 값이 같습니다.')
                                    }

                                }}>수정하기</button><button onClick={() => setModal(false)}>닫기</button>
                            </div>
                        </div>
                        :
                        null
                }

            </div>
        </>

    )
}

export { Mypage };