const buyItemInfo = createSlice({
  name: 'buyItemInfo',
  initialState: [],
  reducers: {
    buyItem(state, action) {
      state.push(action.payload);
    },
    delCart(state, action) {
      for (let i = 0; i < action.payload.length; i++) {
        const delCart = action.payload[i].id;
        const 삭제번호 = state.findIndex((a) => a.id === delCart);
        if (삭제번호 !== -1) {
          state.splice(삭제번호, 1);
        }
      }
    }
  }
})

