const loveItemInfo = createSlice({
  name: 'loveItemInfo',
  initialState: [],
  reducers: {
    loveItem(state, action) {
      state.push(action.payload);
    },
    loveItemDel(state, action) {
      const delNumber = state.findIndex((a) => { return a.id == action.payload });
      state.splice(delNumber, 1);
    }
  }
})

