import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useParams } from 'react-router-dom'
import {delCart} from '../store.js';
import './order.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

function Order() {
    // let { link } = useParams();
    const state = useSelector((state) => state)
    let dispatch = useDispatch();
    // console.log('state.buyItemInfo : ' + state.buyItemInfo[0].기종)
    const [배송메모, 배송메모수정] = useState(['문 앞에 놓아주세요', '부재 시 연락 부탁드려요', '배송 전 미리 연락해 주세요'])
    const [메모선택, 메모선택수정] = useState('배송메모를 선택해주세요')
    const [배송비, 배송비수정] = useState(3000);
    const [총결제금액, 총결제금액수정] = useState(state.buyItemInfo.reduce((totalPrice, item) => totalPrice + item.총금액, 0) + 배송비);

    // 체크박스 변수
    const [checkedId, setCheckedId] = useState(new Set());
    const numChecked = checkedId.size;
    
    
    // 주소값 가져옴
    const location = useLocation();
    const currentPath = location.pathname;
    // 가져온 주소값에 이러한 단어가 포함되어 있는지 확인
    const result_order = currentPath.includes('Order');
    const result_basket = currentPath.includes('Basket');
    
    useEffect(() => {
        // console.log('state.buyItemInfo.id : ' +  state.buyItemInfo[0].id)

    }, [메모선택])

    const updateSet = (set, id) => {
        const updateSet = new Set(set);

        if (updateSet.has(id)) {
            updateSet.delete(id);
        } else {
            updateSet.add(id);
        }

        return updateSet;
    }

    const handleOnChange = (id) => {
        setCheckedId((prevSet) => updateSet(prevSet, id));
    };

    const allCheckedId = ({ target: { checked } }) => {
        if (checked) {
            const allCheckedId = new Set(state.buyItemInfo.map(({ id }) => id));
            // const allCheckedId2 = Array.from(allCheckedId);
            // console.log('allCheckedId2 : ' + allCheckedId2.length)
            setCheckedId(allCheckedId);
        } else {
            setCheckedId(new Set());
        }
    }

    const checkedDelete = () => {
        // 체크된 애들은 set안에 id값이 저장되어 있으므로 id값을 꺼내서
        // 기존 배열하고 비교하고 id값이 같으면 그 배열을 삭제
        const allCheckedId = Array.from(checkedId);
        const delCheckedId = state.buyItemInfo.filter(item => allCheckedId.includes(item.id))
        dispatch(delCart(delCheckedId));
    }

    return (
        <div id='order_wrap'>
            <h1>장바구니 / 주문하기</h1>

            <div className='order_text'>
                <td>
                    <tr style={result_basket ? { color: 'black' } : { color: '#a8a8a8' }}>장바구니</tr>
                    <tr>{'>'}</tr>
                    <tr style={result_order ? { color: 'black' } : { color: '#a8a8a8' }}>주문/결제</tr>
                    <tr>{'>'}</tr>
                    <tr>완료</tr>
                </td>
            </div>

            <section className='order_section'>

                <div className='order_section_high'>
                    <p>주문상품</p>
                    <input type="checkbox" onChange={allCheckedId} checked={numChecked == state.buyItemInfo.length} />
                    <button onClick={checkedDelete}>선택삭제</button>
                    <div><img src={process.env.PUBLIC_URL + '/shop_img/shop_cost.png'} /></div>
                </div>

                {
                    state.buyItemInfo.map(({id},index) => {
                        return (
                            <>
                                <article className='order_section_middle'>
                                    <h4>{index + 1}</h4>

                                    <input type="checkbox" checked={checkedId.has(id)} onChange={() => handleOnChange(id)} />

                                    <div className='middle_img'><img src={process.env.PUBLIC_URL + state.buyItemInfo[index].이미지} /></div>

                                    <div className='middle_text'>
                                        <div>{state.buyItemInfo[index].기종}</div>
                                        <p>{"기종 : " + state.buyItemInfo[index].기종 + ' / 색상 : ' + state.buyItemInfo[index].색상 + ' / 수량 : ' + state.buyItemInfo[index].총수량}</p>
                                        <span>{state.buyItemInfo[index].총금액 + '원'}</span>

                                        <div className="dropdown">
                                            <button className="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                                {메모선택}
                                            </button>
                                            <ul className="dropdown-menu">
                                                {
                                                    배송메모.map((text, index) => {
                                                        return (
                                                            <li><a className="dropdown-item" onClick={() => { 메모선택수정(배송메모[index]) }} href="#">{배송메모[index]}</a></li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                        </div>
                                    </div>
                                </article>
                            </>
                        )
                    })
                }

                <div className='order_section_price'>
                    <div className='price_all'>
                        <div>총 상품가격</div>
                        {/* 0은 초기값이고 item은 각 객체를 의미함 또한 totoalPrice는 변수명임 */}
                        <div>{state.buyItemInfo.reduce((totalPrice, item) => totalPrice + item.총금액, 0)}원</div>
                    </div>

                    <div className='price_delivery'>
                        <div>{배송비}</div>
                        <div>{배송비 + '원'}</div>
                    </div>
                </div>

            </section>

            <div className="order_futter">

                <div className='order_futter_total'>
                    <div>{state.buyItemInfo.reduce((totalPrice, item) => totalPrice + item.총금액, 0)}원</div>
                    <div>+</div>
                    <div>{배송비 + '원'}</div>
                    <div>=</div>
                    <div>{총결제금액 + '원'}</div>
                </div>

                <button className="order_button">
                    주문하기
                </button>
            </div>


            <div className='order_back'></div>
        </div>
    )
}

export { Order };