import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useLocation } from 'react-router-dom'
// import { plusInfo } from '../store.js';
import './lovepage.css';

function Lovepage() {
    let 꺼낸거 = sessionStorage.getItem('LoginData');
    꺼낸거 = JSON.parse(꺼낸거);

    const thepage = ['/Mypage', '/Lovepage'];
    const [mypage, setMypage] = useState(false);
    const [lovepage, setLovepage] = useState(false);
    const [Shoppage, setShoppage] = useState(false);
    const [Orderpage, setOrderpage] = useState(false);
    const location = useLocation();
    const locationData = location.pathname;

    const state = useSelector((state) => state)

    useEffect(() => {

        setMypage(false);
        setLovepage(false);
        setShoppage(false);
        setOrderpage(false);

        for (let i = 0; i < thepage.length; i++) {
            if (locationData === thepage[i]) {
                if (locationData == '/Mypage') {
                    setMypage(true);

                } else if (locationData == '/Lovepage') {
                    setLovepage(true);
                }
            }
        }
    }, [])


    return (
        <>
            <div className='lovePage_title_back'>

                <div className='lovePage_title'>
                    <img src={process.env.PUBLIC_URL + '/mypage_img/my_img.png'} />
                    <div className='lovePage_text'>{꺼낸거.id}님 어서오세요.</div>
                </div>

            </div>

            <div className='lovePage_wrap'>

                <div className="lovePage_info_wrap">
                    <div className='lovePage_info'>
                        <ul>
                            <li><Link className={`${mypage ? 'color' : ''}`} to="/Mypage">내정보</Link></li>
                            <li><Link className={`${Shoppage ? 'color' : ''}`} to="/Order">장바구니</Link></li>
                            <li><Link className={`${lovepage ? 'color' : ''}`} to="/Lovepage">좋아요</Link></li>
                        </ul>
                    </div>

                    <section id='lovePage_catalogue_wrap'>
                        <h1>좋아요 목록</h1>
                        <hr></hr>
                        {
                            state.loveItemInfo.map((item, index) => {
                                
                                return (
                                    <div key={index} className='lovePage_catalogue'>

                                        <div className='lovePage_item'>
                                            <div className='lovePage_item_img'><img src={process.env.PUBLIC_URL + state.loveItemInfo[index].이미지} /></div>

                                            <div>
                                                <h5>ESR</h5>
                                                <p>{state.loveItemInfo[index].기종}</p>
                                                <span>{state.loveItemInfo[index].금액}원</span>
                                                <div className='lovePage_item_img2'><img src={process.env.PUBLIC_URL + '/mypage_img/love.png'} /></div>
                                            </div>
                                        </div>
                                    </div>)
                            })
                        }

                        <div>1</div>
                    </section>
                </div>


            </div>
        </>

    )
}

export { Lovepage };