import React, { Component, useEffect, useState } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom'
import Slider from 'react-slick';
import styled from 'styled-components';
import "./MainSlider/slick.css";
import "./MainSlider/slick-theme.css";
import { Shop } from "../ShopPage/shop.js";

function SubSlide() {

    let navigate = useNavigate();
    const state = useSelector((state) => state)

    const settings = {
        arrows: false,
        dots: true,
        infinite: true,
        // autoplay: true,
        autoplaySpeed: 5000,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4
    };

    return (
        <div className="sub_wrap">
            <div className="sub_title">HaloLock Series</div>

            <Slider {...settings}>
                {
                    state.subimg.map((items, index) => {
                        return (
                            <div key={index} onClick={()=>{navigate('/Shop/' + index)}} className="sub_wrap2">

                                <span className='sub_hover_cover'>
                                    <span className='sub_black_cover'></span>

                                    <div className='sub_img_cover'>
                                        <img className="sub_img cursorPointer" src={process.env.PUBLIC_URL + '/main_img/love.png'} />
                                        <img className="sub_img cursorPointer" src={process.env.PUBLIC_URL + '/main_img/cart.png'} />
                                    </div>
                                </span>

                                <img className="sub_hover_img" src={process.env.PUBLIC_URL + state.subimg[index].url} />
                                <div className="sub_text1">{state.subimg[index].name}</div>
                                <div className="sub_text2">{state.subimg[index].price}</div>
                            </div>
                        )
                    })
                }
            </Slider >
        </div >
    )
}

function SubSlide2() {
    const [slide, setSlide] = useState([
        { id: 0, img: '/main_img/slide_event_1.png' },
        { id: 1, img: '/main_img/slide_event_2.png' },
        { id: 2, img: '/main_img/slide_event_3.png' },
        { id: 3, img: '/main_img/slide_event_4.png' },
    ])

    const [탭, 탭변경] = useState(1);
    const [탭스타일, 탭스타일변경] = useState('transform 500ms ease-in-out')

    // 얘 호출할때 modifiedArray(slide) 일케 쓰면 됨 그럼 알아서 전달인자로 slide가 들어감
    const newDataArray = (arr) => {
        const dataStart = arr[0];
        const dataEnd = arr[arr.length - 1];
        const modifiedArray = [...arr, ...arr, ...arr];
        return modifiedArray
    }

    const nextSlide = () => {
        const slideLength = slide.length; // 4
        const newCurr = 탭 + 1;
        탭변경(newCurr);

        if (newCurr === slideLength + 1) {
            moveToNthSlide(1);
        }

        탭스타일변경('transform 500ms ease-in-out');
    }
    console.log("nextSlide : " + 탭)

    const prevSlide = () => {
        const slideLength = slide.length; // 4
        const newCurr = 탭 - 1;
        탭변경(newCurr);

        if (newCurr === 0) {
            moveToNthSlide(slideLength);
        }

        탭스타일변경('transform 500ms ease-in-out');
    }
    console.log("prevSlide : " + 탭)

    const moveToNthSlide = n => {
        setTimeout(() => {
            탭스타일변경('');
            탭변경(n);
            console.log('탭스타일 : ' + 탭스타일)
        }, 500);
    }

    let SubTitle = {
        marginBottom: '15px',
    }

    return (
        <div className="slide_wrap_all">
            <div className="slide_wrap">

                <div className="sub_title" style={SubTitle}>ESR 쇼핑 스토리</div>
                <div className="sub_text">ESR에서 준비한 쇼핑 아이템을 한눈에</div>

                <div className="slide_button_wrap">
                    {
                        slide.map((item, index) => {
                            return (
                                <div key={index} onClick={() => 탭변경(index + 1)} className={`slide_button ${탭 == index + 1 ? 'slide_button_color' : ''}`}></div>
                            )
                        })
                    }
                    <div onClick={prevSlide} className="slide_button2 cursorPinter">
                        <img src={process.env.PUBLIC_URL + "/main_img/slide_button_1.png"} />
                    </div>

                    <div onClick={nextSlide} className="slide_button2 cursorPinter">
                        <img src={process.env.PUBLIC_URL + "/main_img/slide_button_2.png"} />
                    </div>
                </div>

                <div className="slider_wrap">
                    <div className='slide_move'
                        style={{
                            transform: `translateX(-${탭 * 390}px)`,
                            transition: `${탭스타일}`
                        }}>
                        {
                            newDataArray(slide).map((slide, index) => {
                                return (
                                    <div>
                                        <img key={index} src={process.env.PUBLIC_URL + slide.img} />
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>

            </div>
        </div>
    )
}


export { SubSlide, SubSlide2 };