import React, { Component, useCallback, useEffect, useState } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { useParams, Link, useNavigate } from "react-router-dom";
import Futter from "../MainPage/futter";
import { buyItem, loveItem, loveItemDel, addReview, delReview } from '../store.js';
import { Header, UpHeader } from "../MainPage/Header";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import './shop.css';
import { v4 as uuidv4 } from 'uuid';
import Pagination from "react-js-pagination";

<script>

</script>

function Shop() {

    let { link } = useParams();
    const loginData = JSON.parse(sessionStorage.getItem('LoginData'));
    // const state = useSelector((state) => state)
    const testInfo = useSelector(state => state.testInfo[link])
    const reviewInfo = useSelector(state => state.reviewInfo)
    let dispatch = useDispatch();
    const navigate = useNavigate();
    const [탭, 탭수정] = useState(0);

    const [이미지, 이미지수정] = useState(testInfo.url[link])
    const [기종, 기종수정] = useState('');
    const [색상, 색상수정] = useState('');
    const [가격, 가격수정] = useState(0);
    const [수량, 수량수정] = useState(0);

    const [modal, setModal] = useState(false);
    const [좋아요, 좋아요수정] = useState(false);
    const [reviewModal, setReviewModal] = useState(false);

    const [review, setReview] = useState('');
    // const [sendReview, setSendReview] = useState([
    //     { name: '', text: '', date: '' },
    // ])

    const [shopSubImg, shopSetSubImg] = useState([
        { id: 0, img: '/shop_img/대표소재1.png' },
        { id: 1, img: '/shop_img/대표소재2.png' },
        { id: 2, img: '/shop_img/대표소재3.png' },
        { id: 3, img: '/shop_img/대표소재4.png' }
    ]);

    const [sticky, setSticky] = useState(false);

    // 페이지네이션 구현 변수
    const [activePage, setActivePage] = useState(1);
    const itemsPerPage = 5;

    // 현재 페이지 자르기
    const indexOfLastItem = activePage * itemsPerPage; // 5 10 15 20
    const indexOfFirstItem = indexOfLastItem - itemsPerPage; // 0 5 10 15
    const reviewItems = reviewInfo.slice(indexOfFirstItem, indexOfLastItem) // 0~5, 5~10, 10~15, 15~20

    const handlePageChange = (pageNumber) => {
        setActivePage(pageNumber);
    };

    useEffect(() => {

        if (색상 && 기종) {
            // 기종의 값이랑 state랑 문자열 비교해서 같으면 같은 값의 price값 주셈
            가격수정(testInfo.가격 * 수량);
        } else {
            가격수정(0)
        }

    }, [색상, 기종, 수량]);

    useEffect(() => {
        // 특정 스크롤값에 도달하면 그 값을 변수에 넣고 스크롤값이 5000을 넘으면 상품후기의 색상이 변하고 스크롤값이 6000을 넘으면 상품문의 색상이 변하고
        // 스크롤값이 1085 =< 상세정보의 색상이 바뀐다
        수량수정(0)

        const handleScroll = () => {
            const shopTab = document.getElementById('shop_tab_wrap');

            if (shopTab) {
                const scrollHeight = window.scrollY >= 1085;
                setSticky(scrollHeight)

                if (window.scrollY >= 1085 && window.scrollY < 16000) {
                    // console.log('window.screenY1 : ' + window.scrollY)
                    탭수정(0)
                }
                else if (window.scrollY >= 16000 && window.scrollY < 17400) {
                    // console.log('window.screenY2 : ' + window.scrollY)
                    탭수정(1)
                }
            }
        };
        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        }
    }, [])

    // 상품 상태가 유효한지 확인 후에 구매하기 이동
    const buyToItem = () => {
        if (loginData) { // 로그인 데이터가 있을때
            if (기종 !== '' && 색상 !== '' && 가격 !== 0 && 수량 !== 0) { // 있으면서 다른값도 있을때
                dispatch(buyItem({ id: uuidv4(), 이미지: 이미지, 기종: 기종, 색상: 색상, 총수량: 수량, 총금액: 가격 }));
                navigate('/Order'); // 페이지 이동
            } else { // 있으면서 다른값은 없을때의 모든 상황
                alert('옵션을 선택해주세요');
            }
        } else { // 없으면
            alert('로그인 해주세요');
        }
    };

    // 상품 상태가 유효한지 확인 후에 장바구니에 추가
    const addToCart = () => {
        if (loginData) { // 로그인 데이터가 있을때
            if (기종 !== '' && 색상 !== '' && 가격 !== 0 && 수량 !== 0) { // 있으면서 다른값도 있을때
                handleAddToCart();
                // 3초뒤에 false값을 넣는 modalTime실행
                modalEvent();
            } else { // 있으면서 다른값은 없을때의 모든 상황
                alert('옵션을 선택해주세요');
            }
        } else { // 없으면
            alert('로그인 해주세요');
        }
    };

    const handleAddToCart = () => {
        dispatch(buyItem({ id: uuidv4(), 이미지: 이미지, 기종: 기종, 색상: 색상, 총수량: 수량, 총금액: 가격 }));
        modalTime();
    };

    const addToLove = () => {
        if (loginData) {
            if (!좋아요) {
                dispatch(loveItem({ id: testInfo.id[link], 이미지: testInfo.url[link], 기종: testInfo.기종[link], 금액: testInfo.가격 }))
                좋아요수정(true)
            } else {
                dispatch(loveItemDel({ id: testInfo.id[link] }))
                좋아요수정(false)
            }
        } else {
            alert('로그인을 해주세요')
        }
    }


    const modalTime = () => {
        setTimeout(() => { setModal(false) }, 3000);
    }

    const modalEvent = () => {
        setModal(true);
    }

    const choice_item = ((선택기종) => {
        // console.log("색상 : " + 선택색상);
        기종수정(선택기종);
    })

    const choice_color = ((선택색상) => {
        // console.log("기종 : " + 선택기종);
        색상수정(선택색상);
    })

    const itemMinus = () => {

        if (기종 !== '' && 색상 !== '') {

            if (수량 == 0) {

            } else {
                수량수정(수량 - 1);
            }
        }
    }

    const itemPlus = () => {
        if (기종 !== '' && 색상 !== '') {
            수량수정(수량 + 1);
        }
    }

    const reviewHandle = () => {
        if (loginData) {
            setReviewModal(true);
        }
    }

    const reviewText = useCallback((e) => {
        const result = e.target.value;
        setReview(result);
    })

    const sendReviewText = () => {
        const today = new Date();
        // 현재 날짜를 가져옵니다.
        const formattedDate = `${today.getFullYear()} - ${today.getMonth() + 1} - ${today.getDate()}`;
        // 원하는 형식으로 날짜를 설정합니다.

        if (review) {
            const sendReview = {
                id: uuidv4(),
                name: loginData.id,
                text: review,
                date: formattedDate
            }
            dispatch(addReview(sendReview));
            setReview('');
            setReviewModal(false);
        }
    }

    return (
        <div className="shop_wrap">
            <Header />

            <header className="shop_header">

                <aside className="shop_img">

                    <div className="shop_img_main">
                        <img src={process.env.PUBLIC_URL + testInfo.url[link]} />
                    </div>

                    {
                        testInfo.url.map((item, index) => {
                            if (index !== 0) {
                                return (
                                    <div key={index} className="shop_img_sub">
                                        <img src={process.env.PUBLIC_URL + testInfo.url[index]} />
                                    </div>
                                )
                            }
                        })
                    }

                </aside>

                <aside id="shop_select">

                    <div className="shop_select_name">
                        <h1>{testInfo.기종[link]}</h1>
                        <p>10% <span className="black_text">{testInfo.가격 + '원'}</span></p>
                    </div>

                    <div id="shop_select_sort">

                        <div className="dropdown">
                            <label>기종</label>
                            <button className="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                [필수]옵션을 선택해 주세요.
                            </button>
                            <ul className="dropdown-menu">
                                {
                                    testInfo.기종.map((기종, index) => {
                                        if (index !== 0) {
                                            return (
                                                <li><a className="dropdown-item" onClick={() => choice_item(testInfo.기종[index])} href="#">{testInfo.기종[index]}</a></li>
                                            );
                                        }
                                    })
                                }
                            </ul>
                        </div>


                        <div className="dropdown">
                            <label>색상</label>
                            <button className="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                [필수]옵션을 선택해 주세요.
                            </button>
                            <ul className="dropdown-menu">
                                {
                                    testInfo.색상.map((색상, index) => {
                                        return (
                                            <li><a className="dropdown-item" onClick={() => choice_color(testInfo.색상[index])} href="#">{testInfo.색상[index]}</a></li>
                                        );
                                    })
                                }
                            </ul>
                        </div>

                    </div>

                    <div id="shop_select_color">
                        <p>{기종 + ' / ' + 색상}</p>

                        <div className="btn-group" role="group" aria-label="Basic example">
                            <button type="button" className="btn btn-primary" onClick={itemMinus}><img src={process.env.PUBLIC_URL + "/shop_img/arrow_left.png"} /></button>
                            <button type="button" className="btn btn-primary">{수량}</button>
                            <button type="button" className="btn btn-primary" onClick={itemPlus}><img src={process.env.PUBLIC_URL + "/shop_img/arrow_right.png"} /></button>
                        </div>
                    </div>

                    <div id="shop_select_price">
                        <h1>총 상품 금액</h1>
                        <span className="black_text">{가격 + ' 원'}</span>
                    </div>

                    <div id="shop_select_buy">
                        <button className="shop_select_buy_button" onClick={buyToItem}>구매하기</button>

                        <button className="shop_select_buy_button" onClick={addToLove}>
                            {
                                좋아요 === true ?
                                    <img src={process.env.PUBLIC_URL + "/shop_img/love(2).png"} />
                                    :
                                    <img src={process.env.PUBLIC_URL + "/shop_img/love(1).png"} />
                            }
                        </button>

                        <button className="shop_select_buy_button" onClick={addToCart}>
                            <img src={process.env.PUBLIC_URL + "/shop_img/cart.png"} />
                        </button>
                        {
                            // 장바구니 바로가기 버튼모달
                            modal ?
                                <div id='shop_modal'>
                                    <span>장바구니에 상품이 담겼습니다.</span>
                                    <Link to="/Order" style={{ textDecoration: "none" }}>
                                        <p>장바구니 바로가기</p></Link>
                                </div>
                                :
                                null
                        }
                    </div>
                </aside>

            </header>

            {
                sticky ? (

                    <nav id="shop_tab_wrap">

                        <div className="shop_tab">
                            <a className={`shop_tab_nav ${탭 == 0 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(0) }} href="#shop_itemInfo">상세정보</a>
                            <a className={`shop_tab_nav ${탭 == 1 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(1) }} href="#shop_review">상품후기</a>
                            <a className={`shop_tab_nav ${탭 == 2 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(2) }} href="#shop_return">상품문의</a>
                            <a className={`shop_tab_nav ${탭 == 3 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(3) }} href="#shop_return">반품/교환정보</a>
                        </div>

                        <div className="shop_tab_back"></div>

                    </nav>
                ) : (

                    <nav id="shop_tab_wrap">

                        <nav className="shop_tab">
                            <a className={`shop_tab_nav ${탭 == 0 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(0) }} href="#shop_itemInfo">상세정보</a>
                            <a className={`shop_tab_nav ${탭 == 1 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(1) }} href="#shop_review">상품후기</a>
                            <a className={`shop_tab_nav ${탭 == 2 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(2) }} href="#shop_return">상품문의</a>
                            <a className={`shop_tab_nav ${탭 == 3 ? 'shop_tap_color' : ''}`} onClick={() => { 탭수정(3) }} href="#shop_return">반품/교환정보</a>
                        </nav>

                    </nav>
                )
            }


            <div id="shop_itemInfo">
                <img src={process.env.PUBLIC_URL + "/shop_img/item_info.png"} />
            </div>

            <nav id="shop_review">
                <h1>상품후기</h1>
                <hr />

                <div className="dropdown">
                    <button className="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                        최신순
                    </button>

                    <ul className="dropdown-menu">
                        <li><a className="dropdown-item" href="#">별점순</a></li>
                        <li><a className="dropdown-item" href="#">날짜순</a></li>
                    </ul>

                    <button className="btn btn-secondary" type="button" onClick={reviewHandle}>
                        작성하기
                    </button>
                </div>

                <tbody className="shop_review_comments">
                    {
                        reviewItems.map((item, index) => {
                            return (
                                <>
                                    <hr />
                                    <tr className="shop_comment">
                                        <td>{reviewItems[index].date}</td>
                                        <td>{reviewItems[index].name}</td>
                                        <td>{reviewItems[index].text}</td>
                                        <td className="shop_comment_del" onClick={() => {
                                            dispatch(delReview(reviewItems[index].id))
                                        }
                                        }><button>삭제</button></td>
                                    </tr>
                                </>
                            )
                        })
                    }
                </tbody>

                <Pagination
                    activePage={activePage}
                    itemsCountPerPage={itemsPerPage}
                    totalItemsCount={reviewInfo.length}
                    pageRangeDisplayed={5}
                    onChange={handlePageChange}
                    itemClass="page-item"
                    linkClass="page-link"
                />

                {
                    // 리뷰 모달
                    reviewModal ?
                        <div id='review_modal'>
                            <div className='review_content'>

                                <section className='review_info'>
                                    <div><img src={process.env.PUBLIC_URL + testInfo.url[link]} /></div>
                                    <div className='review_text'>
                                        <div>{testInfo.기종[link]}</div>
                                        <span>{testInfo.가격 + '원'}</span>
                                    </div>
                                </section>

                                <section className='review_input'>
                                    <h6>내용</h6>
                                    <input type="text" onChange={reviewText} />
                                    <button onClick={() => { setReviewModal(false) }}>취소</button>
                                    <button onClick={sendReviewText}>작성하기</button>
                                </section>
                            </div>
                        </div>
                        :
                        null
                }

            </nav>

            <nav id="shop_return">
                <h1>반품/교환정보</h1>
                <hr />
                <img src={process.env.PUBLIC_URL + "/shop_img/payback.png"} />
            </nav>

            <Futter></Futter>
        </div >
    )
}

export { Shop };